/*
VIEW-WRAPPER
 */

// ON LOAD PAGE
.is-load .view-wrapper {
  visibility: hidden;
}

.view-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  width: 100%;
  min-width: 320px;
  min-height: 100%;
  padding: 0;
  overflow: hidden;
}

.container {
  position: relative;
  min-width: 320px;
  max-width: 1170px;
  margin: 0 auto;
  padding: 0;
}

@media screen and (max-width: 1026px){
  .container{
    padding: 0 20px;
  }
}

@media screen and (max-width: 320px){
  .container{
    padding: 0 10px;
  }
}